<template>
  <div class="box">
    <div class="header">
      <van-search
        v-model="value"
        shape="round"
        background="white"
        placeholder="搜索你想要的"
        right-icon="search"
        input-align="center"
        @focus=" goSearch"
      />
    </div>
    <!-- 轮播图 -->
    <div class="banner">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item, index) in images" :key="index">
          <img :src="item.bannerImg" alt />
        </van-swipe-item>
      </van-swipe>
    </div>
    <!-- nav导航 -->
    <div class="nav clearfix">
      <van-grid>
        <div class="div" v-for="(item,index) in imagesNav" :key="index">
          <van-grid-item :icon="item.src" :text="item.text" @click="goClassify(index)" />
        </div>
      </van-grid>
    </div>
    <!-- 3D轮播图 -->
    <h2>您的选择：</h2>
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div
          class="swiper-slide"
          :style="{background: 'url( '+ item.src +') 100% 100%'}"
          v-for="(item,index) in bannerImage"
          :key="index"
          @click="goClassify(index)"
        >{{item.text}}</div>
      </div>

      <!-- Add Pagination -->
      <div class="swiper-pagination"></div>
    </div>

    <!-- 滑动展示区 -->
    <div class="show"></div>

    <!-- 推荐 -->
    <recomment></recomment>
  </div>
</template>

<script>
import Swiper from "swiper";
import recomment from "../../components/recomment.vue";
export default {
  data() {
    return {
      value: "",
      images: [],
      imagesNav: [
        { src: require("../../assets/images/tuijian.jpg"), text: "最新推荐" },
        { src: require("../../assets/images/navnatie.jpg"), text: "拿铁" },
        { src: require("../../assets/images/navrecoment.jpg"), text: "咖啡" },
        { src: require("../../assets/images/timg.jpg"), text: "瑞纳冰" }
      ],
      bannerImage: [
        { src: require("../../assets/images/banner1.jpg"), text: "最新推荐" },
        { src: require("../../assets/images/banner2.jpg"), text: "拿铁" },
        { src: require("../../assets/images/banner3.jpg"), text: "咖啡" },
        { src: require("../../assets/images/banner4.jpg"), text: "瑞纳冰" }
      ]
    };
  },
  mounted() {
    new Swiper(".swiper-container", {
      pagination: ".swiper-pagination",
      effect: "coverflow",
      grabCursor: true,
      centeredSlides: true,
      slidesPerView: "auto",

      coverflow: {
        rotate: 50,
        stretch: 0,
        depth: 100,
        modifier: 1,
        slideShadows: true
      }
    });
  },
  created() {
    this.$store.state.active = 0;

    this.axios({
      method: "get",
      url: "/banner",
      params: {
        appkey: this.appkey
      }
    })
      .then(res => {
        
        if (res.data.code == 300) {
          this.images = res.data.result;
          
        }
      })
      .catch(err => {
        
      });
  },
  components: {
    recomment
  },
  methods: {
    goClassify(index) {
      
      this.$router.push({ name: "分类" });
      this.$store.state.activeKey = index;
    },
    goSearch() {
      this.$router.push({ name: "搜索" });
    }
  }
};
</script>

<style lang="less" scoped>
.box {
    .header {
      padding-top: 3px;
      padding-bottom: 6px;
      background-color: white;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 10000;
      width: 100%;
      /deep/ .van-field__left-icon {
        margin-right: 0.13333rem;
        display: none;
      }
    }
    .banner {
      width: 98%;
      margin: 10px auto;
      margin-top: 64px;
      img {
        width: 95%;
        border-radius: 10px;
        display: block;
        margin: 0 auto;
      }
    }
    h2 {
      margin-left: 20px;
    }
    .nav {
      width: 92%;
      margin: 10px auto;
      border-radius: 20px;
      /deep/ .van-icon__image {
        width: 2em;
        height: 2em;
        object-fit: contain;
        border-radius: 50%;
      }
    }
  }
  
  body {
    background: #fff;
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 14px;
    color: #000;
    margin: 0;
    padding: 0;
  }
  .swiper-container {
    width: 95%;
    text-align: center;
  }
  .swiper-slide {
    background-position: center;
    background-size: cover;
    width: 150px;
    height: 150px;
    margin: 0 auto;
    text-align: center;
    line-height: 150px;
    font-size: 18px;
    border-radius: 20px;
  }
  
  .div {
    width: 25%;
  }
</style>